import React from 'react';
import Widget from './Widget';

import './styles/Photos.css';

class Photos extends Widget {
  async getUpdateData() {
    if(!this.state.images) {
      const images = (await this.fetchJson(`/api/photos/${this.layoutConfig.dir}`)).sort(() => Math.random() > 0.5 ? 1 : -1);
      this.preloadImages(images);
      return { name: 'photos', images, currentImage: 0 };
    }
    return { 
      ...this.state, 
      currentImage: this.state.currentImage < this.state.images.length-1 ? this.state.currentImage+1 : 0,
      updateInterval: 60000
    };
  }
  getImageUri(image, folder='tom') {
    return `/api/photo/${encodeURIComponent(folder + '/' + image)}`;
  }
  preloadImages(images) {
    const fragment = document.createDocumentFragment();
    images.forEach(i => {
      console.log('preload', this.getImageUri(i));
      const linkEl = document.createElement('link');
      linkEl.setAttribute('rel', 'preload');
      linkEl.setAttribute('href', this.getImageUri(i));
      linkEl.setAttribute('as', 'image');
      fragment.appendChild(linkEl);
    });
    document.head.appendChild(fragment);
  }
  renderInner() {
    if(this.state.currentImage >= 0) {
      const style = {
        'background-image': `url(${this.getImageUri(this.state.images[this.state.currentImage])})`,
        'background-size': 'cover'
      };
      return <div className="Image" style={style}></div>
    }
  }
}

export default Photos;